import {Directive, ElementRef, OnInit} from '@angular/core';

import 'prismjs/components/prism-bash.js';
import 'prismjs/plugins/toolbar/prism-toolbar.js';
import 'prismjs/plugins/command-line/prism-command-line.js';

import * as prism from 'prismjs';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

declare const Prism: typeof prism;

@Directive({
  selector: '[adaptCode], [adapt-code]'
})

export class AdaptCodeHighlighterDirective extends AdaptRadarAngularGenericDirective implements OnInit {

  constructor(private el: ElementRef,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptCodeHighlighter);
  }

  ngOnInit(): void {
    Prism.highlightElement(this.el.nativeElement);
  }
}
